<template>
    <div class="dashboard">
        <h1 class="subheading">Fletton Quays - Price Work - Peterborough</h1>

        <v-container class="my-5">

            <v-card  v-for="project in projects" :key="project.title">
                <v-layout row wrap :class="`pa-3 project ${project.status}`">
                    <v-flex xs12 sm4 md4>
                        <div class="caption grey--text">Plot Number</div>
                        <div>{{ project.plot }}</div>
                    </v-flex>
                    <v-flex xs6 sm4 md2>
                        <div class="caption grey--text">Start date</div>
                        <div>{{ project.startdate }}</div>
                    </v-flex>
                    <v-flex xs6 sm4 md2>
                        <div class="caption grey--text">Finish date</div>
                        <div>{{ project.finishdate }}</div>
                    </v-flex>
                    <v-flex xs6 sm4 md2>
                        <div class="caption grey--text">Price</div>
                        <div>{{ project.price}}</div>
                    </v-flex>
                    <v-flex xs2 sm4 md2>
                        
                    <div class="right">
                        <v-chip small :class="`${project.status} white--text caption my-2`">{{ project.status }}</v-chip>
                    </div>
                    </v-flex>
                </v-layout>
                <v-divider></v-divider>
            </v-card>

        </v-container>
    
    </div>
</template>

<script>
import db from '@/fb'

export default {
    data(){
        return {
            projects: [
                {plot: '59 one bed flat', startdate: '27th May 2019', finishdate: '27th May 2019', price: '230', status: 'complete'},
                {plot: '227 two bed flat', startdate: '28th May 2019', finishdate: '29th May 2019', price: '220', status: 'complete'},
                {plot: '228 two bed flat', startdate: '28th May 2019', finishdate: '29th May 2019', price: '220', status: 'complete'},
                {plot: '204 two bed flat', startdate: '30th May 2019', finishdate: '30th May 2019', price: '220', status: 'complete'}
            ]
        }
    },

    methods: {

    },
    created() {
        db.collection('flat-price').onSnapshot(res => {
            const changes = res.docChanges();

            changes.forEach(change => {
                if(change.type === 'added') {
                    this.projects.push({
                        ...change.doc.data(),
                        id: change.doc.id
                    })
                }
            })
        })
    }
}
</script>

<style>

.project.complete {
    border-left: 4px solid #3cd1c2;
}
.project.ongoing {
    border-left: 4px solid orange;
}
.project.overdue {
    border-left: 4px solid tomato;
}
.v-chip.complete {
    background: #3cd1c2;
}
.v-chip.ongoing {
    background: #ffaa2c;
}
.v-chip.overdue {
    background: #f83e70;
}
</style>


