<template>
  <v-dialog max-width="600px" v-model="dialog">
    <v-btn slot="activator" class="success">Add a new flat</v-btn>
    <v-card>
      <v-card-title>
        <h2>Add a new flat</h2>
      </v-card-title>
      <v-card-text>
        <v-form class="px-3" ref="form">
          <v-text-field label="Plot Number" v-model="plot" :rules="inputRules"></v-text-field>

          <v-menu>
            <v-text-field :value="formattedDate" slot="activator" label="Start Date" :rules="inputRules"></v-text-field>
            <v-date-picker v-model="startdate"></v-date-picker>
          </v-menu>
          <v-spacer></v-spacer>
          <v-menu>
            <v-text-field :value="formattedDate" slot="activator" label="Finish Date" :rules="inputRules"></v-text-field>
            <v-date-picker v-model="finishdate"></v-date-picker>
          </v-menu>
          <v-text-field label="Price" v-model="price" :rules="inputRules"></v-text-field>
          <v-btn class="success mx-0 mt-3" @click="submit" :loading="loading">Add flat</v-btn>
        </v-form>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import format from 'date-fns/format'

import db from '@/fb'
export default {
  data() {
    return {
      plot: '',
      price: '', 
      startdate: '',
      finishdate: '',
      inputRules: [
        v => v.length >=3 || 'Minimum length is 3 characters'
      ],

      loading: false,
      dialog: false
    }
  },
  methods: {
    submit() {
      if(this.$refs.form.validate()) {
        this.loading = true;


      const flat = {
        plot: this.plot,
        price: this.price,
        startdate: format(this.startdate, 'Do MMM YYYY'),
        finishdate: format(this.finishdate, 'Do MMM YYYY'),
        status: 'complete'
      }
      db.collection('flat-price').add(flat).then(() => {
        this.loading = false;
        this.dialog = false;
        this.$emit('Added')
      })



      }
    }
  },
  computed: {
    formattedDate() {
      return this.startdate ? format(this.startdate, 'Do MMM YYYY') : ''
    },
    formattedDate() {
      return this.finishdate ? format(this.finishdate, 'Do MMM YYYY') : ''
    }
  }
}
</script>

